.Navigation {
  display: grid;
  grid-template-columns: 1fr 3fr 6fr 2fr 1fr;
  gap: 0.5rem;
  align-items: center;
  height: 5rem;
  background-color: black;
  color: white;
}

.app-brand {
  grid-column: 2/3;
  font-weight: bold;
  font-size: larger;
}
.app-quote {
  grid-column: 3/4;
  text-align: center;
}
.app-button-cont {
  grid-column: 4/5;
  justify-self: end;
}

