.app-training-keeper {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr 5fr 0.3fr 0.3fr 0.3fr;
  grid-template-rows: 40px;
  color: white;
}
.save-btn {
  grid-column: 6/7;
  grid-row: 2/3;
}

.app-training-keeper > div {
  height: 100%;
  width: 100%;
  border: 0.5px solid #23120b;
  background-color: darkgoldenrod;
  text-align: center;
}

.training-list {
  grid-column: 2/3;
  grid-row: 2/3;
}

.topic-list {
  grid-column: 4/5;
  grid-row: 2/3;
}
.app-training-keeper ul {
  display: grid;
  grid-auto-rows: 40px;
  padding-inline-start: 0;
}
.app-training-keeper li {
  height: 100%;
  width: 100%;
  list-style-type: none;
  justify-self: center;
  align-self: center;
}
.app-training-keeper li:hover {
  background-color: black;
}
.list-header > h2 {
  margin: 0;
}

/*-------------------------------------------
-----> List header */
.list-header > button {
  display: none;
}
.list-header:hover {
  display: grid;
  grid-template-columns: 1fr 40px;
}
.list-header:hover > button {
  display: inline-block;
}

.topic-list li {
  background-color: #fdb827;
} /*----------------------------------------------
----> Add form */
.backdrop {
  position: absolute;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: black;
  opacity: 50%;
  z-index: 2;
}

.add-training {
  --form-w: calc(100vw * 0.5);
  --form-h: calc(100vh * 0.5);
  width: var(--form-w);
  height: var(--form-h);
  position: absolute;
  background-color: white;
  left: calc(50% - (var(--form-w) * 0.5));
  top: calc(50% - (var(--form-h) * 0.5));
  z-index: 3;
  text-align: center;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 3fr 1fr;
  grid-template-rows: 2fr 1fr 1fr 3fr 1fr 1fr 1fr;
  row-gap: 10px;
}
.add-training h2 {
  grid-column: 2/4;
}
.add-training label {
  font-weight: bold;
  justify-self: left;
  grid-column: 2/3;
}
.add-training input {
  width: 100%;
  grid-column: 3/4;
  padding: 0;
  border: 0.0001rem solid black;
  /* margin: 10px 0; */
}
.add-training textarea {
  width: 100%;
  height: 100%;
  grid-column: 3/4;
  grid-row: 3/5;
  padding: 0;
  border: 0.0001rem solid black;
}
.add-training button {
  width: 100%;
  grid-column: 3/4;
  grid-row: 6/7;
  padding: 0;
  border: 0.0001rem solid black;
}

/*--------------------------------------------
----> All list */

.training-list li {
  height: 100% !important;
  display: grid;
  grid-template-columns: 1fr 40px;
  grid-template-rows: 40px;
  align-items: center;
  background-color: #f1a877;
}

.green {
  color: green;
}
.black {
  background-color: black !important;
}

/*----------------------------------------------
-----> Topic list */
.d-none {
  display: none !important;
}

.topic-list-item {
  display: grid;
  grid-template-columns: 1fr 40px;
  grid-template-rows: 40px;
  align-items: center;
  justify-self: center;
}

.topic-list-item.option {
  grid-template-columns: 1fr 40px 40px;
  grid-template-rows: 40px;
}

.topic-list-item > * {
  height: 100%;
  display: grid;
  align-items: center;
}

/*-------------------------------------------
------> Training List Item */
.training-list-item > * {
  height: 100%;
  display: grid;
  align-items: center;
}
