.app-button > div {
  --btn-dim: 2rem;
  width: var(--btn-dim);
  height: var(--btn-dim);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0.4rem;
  transition: transform 1s;
}

.block {
  width: 100%;
  height: 100%;
  border: 1px solid white;
  transition: transform 1s;
}

.app-button:hover > div {
  transform: rotateZ(0);
  transition: transform 0.5s;
  transform: rotateZ(45deg);
}

.app-button:active > div .tl {
  transform: translateX(0) translateY(0) rotateZ(0);
  transition: transform 0.01s;
  transform: translateX(-1rem) translateY(1rem) rotateZ(-90deg);
}
.app-button:active > div .tr {
  transform: translateX(0) translateY(0) rotateZ(0);
  transition: transform 0.01s;
  transform: translateX(-1rem) translateY(-1rem) rotateZ(-90deg);
}

.app-button:active > div .bl {
  transform: translateX(0) translateY(0) rotateZ(0);
  transition: transform 0.01s;
  transform: translateX(1rem) translateY(1rem) rotateZ(-90deg);
}
.app-button:active > div .br {
    transform: translateX(0) translateY(0) rotateZ(0);
    transition: transform 0.01s;
    transform: translateX(1rem) translateY(-1rem) rotateZ(-90deg);
  }
